import Vue from 'vue'
import Router from 'vue-router'
//我的活动路由
import home1 from '../components/home/home1/home1'

import home2 from '../components/home/home2'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name:'home1',
      component: home1,
    },
    {
      path: '/home2',
      name:'home2',
      component: home2
    }
  ]
})



